/*
HAMBURGER MENU
*/

/* Individual item */
.bm-item {
    display: inline-block;
    text-decoration: none;
    margin-bottom: 10px;
    color: #d1d1d1;
    transition: color 0.2s;
}

/* Change color on hover */
.bm-item:hover {
    color: white;
}

  
/* Position and sizing of burger button */
.bm-burger-button {
    position: fixed;
    width: 40px;
    height: 40px;
    right: 45px;
    top: 10px;
}
  
/* Color/shape of burger icon bars */
.bm-burger-bars {
    background: #373a47;
}
  
/* Position and sizing of clickable cross button */
.bm-cross-button {
    height: 24px;
    width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
    background: #bdc3c7;
}

/* General sidebar styles */
.bm-menu {
    background: #373a47;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
}

.bm-menu-wrap {
    width: 100% !important;
    height: auto !important;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
    fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
    color: #b8b7ad;
}

/* Styling of overlay */
.bm-overlay {
    background: rgba(0, 0, 0, 0.3);
}
img {
    display: inline-block;
    margin: 0 20px 0 20px;
}

.caption {
    display: none;
    width: 100%;
    position: absolute;
    bottom: 5%;
    left: 0;
    background: rgba(0,0,0,.7);
    color: white;
}

.caption span {
    border-bottom: 1px solid black
}

.image {
    display: inline-block;
    position: relative;
}

.image:hover > .caption {
    display: block;
    overflow-y: hidden;
}


.row {
    text-align: center;
}

.book {
    width: 200px;
    height: 160px;
    float: left;
    position: relative;
}

.bookImage {
    height: 150px;
}

.bookInfo {
    display: none;
    width: 100%;
    height: 80%;
    position: absolute;
    bottom: 0;
    left: 0;
    background: rgba(0,0,0,.7);
    color: white;
}

.bookInfo > p, .bookInfo > a {
    font-size: 1rem;
    line-height: 1.1rem;
    padding: 5px;
}

.book:hover > .bookInfo {
    display: block;
    overflow-y: hidden;
}

.author, .dateRead {
    font-size: .8rem !important;
}

.dateRead {
    font-style: italic;
}


